<template>
  <div class="va-pa-4">
    <!-- üîç Í≤ÄÏÉâ ÌïÑÌÑ∞ -->
    <div class="search-grid va-mb-4">
      <va-input
        v-model="filters.workOrder"
        label="ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏"
        placeholder="ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏ ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.productName"
        label="Ï†úÌíàÎ™Ö"
        placeholder="Ï†úÌíàÎ™Ö ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.productSpec"
        label="Ï†úÌíàÍ∑úÍ≤©"
        placeholder="Ï†úÌíà Í∑úÍ≤© ÏûÖÎ†•"
        readonly
      />
      <va-input
        v-model="filters.resultId"
        label="ÏûëÏóÖÏã§Ï†Å ID"
        placeholder="ÏûëÏóÖ Ïã§Ï†Å ID ÏûÖÎ†•"
        readonly
      />
      <va-date-input
        v-model="filters.workDate"
        label="ÏûëÏóÖÏùºÏûê"
        :manual-input="false"
        :clearable="true"
      />
      <va-input v-model.number="selectedItem.final_qty" label="ÏµúÏ¢Ö ÏàòÎüâ" />

      <div class="button-group va-mt-2">
        <va-button color="info" @click="openProductPopup"
          >ÏûëÏóÖÏßÄÏãú Í≤ÄÏÉâ</va-button
        >
        <va-button color="primary" @click="searchItems">Ï°∞Ìöå</va-button>
        <va-button color="secondary" @click="resetFilters">Ï¥àÍ∏∞Ìôî</va-button>
      </div>
    </div>

    <!-- üìã Ïã§Ï†Å ÌÖåÏù¥Î∏î -->
    <va-data-table :items="workList" :columns="columns" track-by="result_id">
      <template #cell(work_start_time)="{ row }">
        {{ formatTime(row.rowData.work_start_time) }}
      </template>
      <template #cell(work_end_time)="{ row }">
        {{ formatTime(row.rowData.work_end_time) }}
      </template>
      <template #cell(duration)="{ row }">
        {{
          calculateDuration(
            row.rowData.work_start_time,
            row.rowData.work_end_time,
          )
        }}
      </template>
    </va-data-table>

    <!-- üîç Ï†úÌíà Í≤ÄÏÉâ ÌåùÏóÖ -->
    <ProductSearchModal
      v-model:visible="isProductPopupOpen"
      @apply="applyProduct"
    />
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import axios from "axios";
import ProductSearchModal from "../modals/ProductSearchModal.vue";

// ‚úÖ Î™®Îã¨ ÏÉÅÌÉú
const isProductPopupOpen = ref(false);
const openProductPopup = () => {
  isProductPopupOpen.value = true;
};

// ‚úÖ ÌïÑÌÑ∞ Îç∞Ïù¥ÌÑ∞
const filters = ref({
  workOrder: "",
  productName: "",
  productSpec: "",
  resultId: "",
  workDate: "",
});

// ‚úÖ ÏµúÏ¢Ö ÏàòÎüâ
const selectedItem = ref({
  final_qty: 0,
});

// ‚úÖ ÏûëÏóÖ Ïã§Ï†Å Î¶¨Ïä§Ìä∏
const workList = ref<any[]>([]);

// ‚úÖ ÌÖåÏù¥Î∏î Ïª¨Îüº
const columns = [
  { key: "result_id", label: "Ïã§Ï†ÅID" },
  { key: "process_code", label: "Í≥µÏ†ïÏΩîÎìú" },
  { key: "process_name", label: "Í≥µÏ†ïÎ™Ö" },
  { key: "work_start_time", label: "ÏãúÏûëÏãúÍ∞Ñ" },
  { key: "work_end_time", label: "Ï¢ÖÎ£åÏãúÍ∞Ñ" },
  { key: "duration", label: "ÏÜåÏöîÏãúÍ∞Ñ(Î∂Ñ)" },
];

const formatTime = (value: string | null): string => {
  if (!value) return "-";
  const date = new Date(value);
  return date.toLocaleTimeString("ko-KR", {
    hour: "2-digit",
    minute: "2-digit",
  });
};

const calculateDuration = (start: string, end: string | null): string => {
  if (!start || !end) return "-";
  const startTime = new Date(start);
  const endTime = new Date(end);
  const diffMs = endTime.getTime() - startTime.getTime();
  const diffMin = Math.floor(diffMs / 60000);
  return `${diffMin}Î∂Ñ`;
};

const applyProduct = (selected: any) => {
  filters.value.workOrder =
    selected.work_order_no || selected.workOrderNo || "";
  filters.value.productName =
    selected.product_name || selected.productName || "";
  filters.value.productSpec =
    selected.product_stand || selected.productSpec || "";
  filters.value.resultId = selected.result_id || selected.resultId || "";
  isProductPopupOpen.value = false;
};

// ‚úÖ Ïã§Ï†Å Ï°∞Ìöå
const searchItems = async () => {
  try {
    if (!filters.value.workOrder || !filters.value.productSpec) {
      alert("ÏûëÏóÖÏßÄÏãúÏÑú Î≤àÌò∏ ÎòêÎäî Ï†úÌíàÍ∑úÍ≤©Ïù¥ ÎàÑÎùΩÎêòÏóàÏäµÎãàÎã§.");
      return;
    }

    const res = await axios.get(
      `/prodResult/${filters.value.workOrder}/${filters.value.productSpec}`,
    );
    const mapped = res.data.map((item: any) => ({ ...item, selected: false }));
    workList.value = mapped;
  } catch (err) {
    alert("Ïã§Ï†Å Ï°∞Ìöå Ïã§Ìå®");
    console.error(err);
  }
};

// ‚úÖ ÌïÑÌÑ∞ Ï¥àÍ∏∞Ìôî
const resetFilters = () => {
  filters.value = {
    workOrder: "",
    productName: "",
    productSpec: "",
    resultId: "",
    workDate: "",
  };
  selectedItem.value.final_qty = 0;
  workList.value = [];
};
</script>

<style scoped>
.search-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.button-group {
  grid-column: span 3;
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}
</style>
